/* ===== Blur Overlay ===== */
.metric-modal-overlay {
  position: fixed;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 998;
  animation: overlayFadeIn 0.5s var(--ease-smooth);
}

@keyframes overlayFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* ===== Card Clone ===== */
.metric-modal-card-clone {
  z-index: 999;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.clone-circle {
  position: relative;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow:
    0 12px 48px rgba(0, 0, 0, 0.12),
    0 0 0 1px rgba(255, 255, 255, 0.3);
}

.clone-progress-ring {
  position: absolute;
  top: 0;
  left: 0;
}

.clone-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 2;
}

.clone-label {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.04em;
  margin-bottom: 8px;
}

.clone-main {
  display: flex;
  align-items: center;
  gap: 6px;
}

.clone-arrow {
  font-size: 28px;
  font-weight: 900;
  line-height: 1;
}

.clone-percent {
  font-size: 36px;
  font-weight: 800;
  letter-spacing: -0.03em;
  line-height: 1;
}

.clone-change {
  margin-top: 6px;
  font-size: 14px;
  font-weight: 600;
}

/* ===== Info Panel ===== */
.metric-modal-panel {
  z-index: 999;
  background: rgba(255, 255, 255, 0.55);
  backdrop-filter: blur(40px) saturate(1.4);
  -webkit-backdrop-filter: blur(40px) saturate(1.4);
  border: 1px solid rgba(255, 255, 255, 0.45);
  border-radius: 20px;
  overflow-y: auto;
  box-shadow:
    0 12px 48px rgba(0, 0, 0, 0.12),
    0 0 0 1px rgba(255, 255, 255, 0.2);
  transform-origin: left center;
  animation: panelGenieIn 0.7s var(--ease-genie);
  will-change: transform, opacity;
}

@keyframes panelGenieIn {
  0% {
    opacity: 0;
    transform: translateX(40px) scale(0.85);
    filter: blur(4px);
  }
  25% {
    opacity: 1;
    filter: blur(0);
  }
  50% {
    opacity: 1;
    transform: translateX(-4px) scale(1.015);
    filter: blur(0);
  }
  75% {
    opacity: 1;
    transform: translateX(1px) scale(0.998);
    filter: blur(0);
  }
  100% {
    opacity: 1;
    transform: translateX(0) scale(1);
    filter: blur(0);
  }
}

/* ===== Panel Inner Styles ===== */
.modal-header {
  display: flex;
  align-items: center;
  padding: 16px 20px 0;
  gap: 8px;
}

.modal-header h2 {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: -0.03em;
  margin: 0;
}

.modal-unit-badge {
  background: rgba(249, 99, 2, 0.1);
  padding: 3px 8px;
  border-radius: 10px;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--accent);
}

.modal-beta-badge {
  padding: 4px 10px;
  background: rgba(238, 113, 37, 0.85);
  color: white;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.1em;
  border-radius: 12px;
  text-transform: uppercase;
}

.modal-close {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: var(--text-tertiary);
  transition: background var(--duration-fast) var(--ease-smooth),
              color var(--duration-fast) var(--ease-smooth),
              transform var(--duration-fast) var(--ease-out-expo);
  margin-left: auto;
}

.modal-close:hover {
  background: rgba(0, 0, 0, 0.05);
  color: var(--text-primary);
  transform: rotate(90deg);
}

.modal-close:active {
  transform: rotate(90deg) scale(0.85);
  transition-duration: var(--duration-instant);
}

.modal-body {
  padding: 16px 20px;
}

.period-section {
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}

.period-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.period-header h3 {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 0.04em;
  margin: 0;
}

.period-lift {
  font-size: 14px;
  font-weight: 700;
  color: var(--neutral);
  display: flex;
  align-items: center;
  gap: 4px;
}

.period-lift .lift-arrow {
  font-weight: 900;
  font-size: 15px;
  line-height: 1;
}

.period-lift.positive {
  color: var(--positive);
}

.period-lift.negative {
  color: var(--negative);
}

.period-data {
  display: flex;
  gap: 40px;
}

.data-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.data-label {
  font-size: 12px;
  font-weight: 500;
  color: var(--text-tertiary);
}

.data-value {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: -0.02em;
}

.data-value.muted {
  color: var(--text-secondary);
  font-weight: 600;
}

.data-value.beta-placeholder {
  color: #667eea;
  font-style: italic;
  font-weight: 600;
}

.comp-section {
  background: rgba(238, 113, 37, 0.06);
  margin: 0 -20px -16px;
  padding: 16px 20px;
  border-radius: 0 0 20px 20px;
}

.comp-header {
  margin-bottom: 8px;
}

.comp-header h3 {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 0.04em;
  margin: 0;
}

.comp-result {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 8px;
}

.comp-arrow {
  font-size: 28px;
  font-weight: 900;
  color: var(--neutral);
  line-height: 1;
}

.comp-result.positive .comp-arrow {
  color: var(--positive);
}

.comp-result.negative .comp-arrow {
  color: var(--negative);
}

.comp-value {
  font-size: 32px;
  font-weight: 800;
  color: var(--text-primary);
  letter-spacing: -0.04em;
  line-height: 1;
}

.comp-result.positive .comp-value {
  color: var(--positive);
}

.comp-result.negative .comp-value {
  color: var(--negative);
}

.comp-explanation {
  font-size: 12px;
  color: var(--text-secondary);
  line-height: 1.4;
  margin: 0;
}

/* ===== Mobile ===== */
@media (max-width: 768px) {
  .metric-modal-card-clone {
    display: none;
  }

  .metric-modal-panel {
    position: fixed !important;
    left: 16px !important;
    right: 16px !important;
    bottom: 16px !important;
    top: auto !important;
    width: auto !important;
    max-height: 70vh !important;
    border-radius: 20px;
    transform-origin: bottom center;
    animation: panelGenieUp 0.7s var(--ease-genie);
    will-change: transform, opacity;
  }

  @keyframes panelGenieUp {
    0% {
      opacity: 0;
      transform: translateY(60%) scale(0.88);
    }
    25% {
      opacity: 1;
      transform: translateY(10%) scale(0.97);
    }
    50% {
      opacity: 1;
      transform: translateY(-2%) scale(1.01);
    }
    75% {
      opacity: 1;
      transform: translateY(0.5%) scale(0.999);
    }
    100% {
      opacity: 1;
      transform: translateY(0) scale(1);
    }
  }

  .comp-value {
    font-size: 28px;
  }

  .comp-arrow {
    font-size: 24px;
  }
}
