.dashboard {
  min-height: 100vh;
  background: var(--background);
}

.dashboard-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: 16px;
  animation: loadingFadeIn var(--duration-slow) var(--ease-smooth) both;
}

@keyframes loadingFadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.dashboard-loading .loading-spinner {
  width: 32px;
  height: 32px;
  border: 2px solid var(--orange-light);
  border-top-color: var(--orange-primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.dashboard-loading p {
  font-size: 15px;
  color: var(--text-secondary);
}

.dashboard-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 40px 80px;
  animation: contentFadeIn 1s var(--ease-out-expo) 0.15s both;
}

@keyframes contentFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 40px;
  text-align: center;
  margin-top: 60px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 24px;
  animation: emptyStateIn 1.2s var(--ease-out-expo) both;
}

@keyframes emptyStateIn {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.97);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.empty-icon {
  animation: emptyIconFloat 3s ease-in-out infinite;
}

@keyframes emptyIconFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}

.empty-icon {
  color: var(--text-tertiary);
  margin-bottom: 24px;
}

.empty-state h2 {
  font-size: 22px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.empty-state p {
  font-size: 15px;
  color: var(--text-secondary);
}

/* Metrics Section */
.metrics-section {
  margin-bottom: 60px;
  animation: sectionSlideUp 1s var(--ease-out-expo) 0.3s both;
}

@keyframes sectionSlideUp {
  from {
    opacity: 0;
    transform: translateY(24px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.section-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-bottom: 48px;
  text-align: center;

}

.section-header h2 {
  font-size: 24px;
  font-weight: 700;
  color: rgba(63, 63, 63, 0.9);
  letter-spacing: -0.03em;
  margin: 0;

}

.period-badge {
  font-size: 14px;
  font-weight: 600;
  color: var(--orange-primary);
  background: var(--orange-light);
  padding: 8px 20px;
  border-radius: 24px;
}

/* Metrics Container - Two Rows */
.metrics-container {
  display: flex;
  flex-direction: column;
  gap: 48px;
}

.metrics-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.row-label {
  font-size: 11px;
  font-weight: 600;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.metrics-row-cards {
  display: flex;
  justify-content: center;
  gap: 40px;
  padding: 20px;
}

/* Chart Section */
.chart-section {
  margin-top: 20px;
  animation: sectionSlideUp 1s var(--ease-out-expo) 0.6s both;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  gap: 16px;
}

.chart-title h2 {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  letter-spacing: -0.02em;
  margin: 0;
}

.chart-controls {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Suggested Events Button */
.suggested-events-btn {
  position: relative;
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  font-size: 12px;
  font-weight: 600;
  color: var(--text-tertiary);
  background: rgba(0, 0, 0, 0.04);
  border: 1px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  animation: chartToggleGlow 4s ease-in-out infinite;
}

.suggested-events-btn:hover {
  color: var(--text-secondary);
  background: rgba(0, 0, 0, 0.06);
  border-color: rgba(0, 0, 0, 0.08);
}

.suggested-events-btn.active {
  background: rgba(238, 113, 37, 0.1);
  color: var(--orange-primary);
  border-color: rgba(238, 113, 37, 0.3);
}

.suggested-events-check {
  display: none;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}

.suggested-events-btn.active .suggested-events-check {
  display: block;
}

/* Glass-style Tooltip */
.suggested-events-tooltip {
  position: absolute;
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
  padding: 8px 12px;
  background: rgba(238, 113, 37, 0.075);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: var(--text-primary);
  font-size: 11px;
  font-weight: 600;
  line-height: 1.4;
  white-space: nowrap;
  border-radius: 10px;
  box-shadow:
    0 4px 24px rgba(0, 0, 0, 0.12),
    0 0 16px rgba(238, 113, 37, 0.1);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.15s ease;
  z-index: 100;
}

.suggested-events-tooltip::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 6px solid transparent;
  border-top-color: rgba(255, 255, 255, 0.3);
}

.suggested-events-btn:hover .suggested-events-tooltip {
  opacity: 1;
  visibility: visible;
}

/* Display Toggle */
.chart-display-toggle {
  display: flex;
  gap: 2px;
  padding: 3px;
  background: rgba(0, 0, 0, 0.04);
  border-radius: 10px;
  animation: chartToggleGlow 4s ease-in-out infinite;
}

@keyframes chartToggleGlow {
  0%, 100% {
    box-shadow: 0 0 8px rgba(238, 113, 37, 0.2), 0 0 2px rgba(238, 113, 37, 0.1);
  }
  50% {
    box-shadow: 0 0 16px rgba(238, 113, 37, 0.4), 0 0 6px rgba(238, 113, 37, 0.25);
  }
}

.display-toggle-btn {
  position: relative;
  padding: 8px 14px;
  font-size: 12px;
  font-weight: 600;
  color: var(--text-tertiary);
  border-radius: 8px;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: color var(--duration-fast) var(--ease-smooth),
              background var(--duration-normal) var(--ease-out-expo),
              box-shadow var(--duration-normal) var(--ease-out-expo),
              transform var(--duration-fast) var(--ease-out-expo);
}

.display-toggle-btn:hover:not(.active) {
  color: var(--text-secondary);
  background: rgba(0, 0, 0, 0.04);
}

.display-toggle-btn:active {
  transform: scale(0.94);
  transition-duration: var(--duration-instant);
}

.display-toggle-btn.active {
  background: var(--white);
  color: var(--orange-primary);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  animation: toggleSlideIn var(--duration-normal) var(--ease-spring);
}

@keyframes toggleSlideIn {
  from { transform: scale(0.9); opacity: 0.6; }
  to { transform: scale(1); opacity: 1; }
}

/* Glass-style Tooltip for Display Toggle */
.display-toggle-tooltip {
  position: absolute;
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
  padding: 8px 12px;
  background: rgba(238, 113, 37, 0.075);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: var(--text-primary);
  font-size: 11px;
  font-weight: 600;
  line-height: 1.4;
  white-space: nowrap;
  border-radius: 10px;
  box-shadow:
    0 4px 24px rgba(0, 0, 0, 0.12),
    0 0 16px rgba(238, 113, 37, 0.1);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.15s ease;
  z-index: 100;
}

.display-toggle-tooltip::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 6px solid transparent;
  border-top-color: rgba(255, 255, 255, 0.3);
}

.display-toggle-btn:hover .display-toggle-tooltip {
  opacity: 1;
  visibility: visible;
}

/* Chart Tags Reset Button */
.chart-tags-reset-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border-radius: 8px;
  padding: 4px;
  height: 36px;
  animation: chartToggleGlow 4s ease-in-out infinite;
}

.chart-tags-reset-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px 12px;
  background: transparent;
  border: none;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  color: var(--text-tertiary);
  cursor: pointer;
  transition: all 0.15s ease;
  white-space: nowrap;
}

.chart-tags-reset-btn:hover {
  color: var(--text-primary);
}

.chart-tags-reset-btn:active {
  transform: scale(0.98);
}

.chart-tags-reset-btn svg {
  flex-shrink: 0;
  transition: transform var(--duration-slow) var(--ease-out-expo);
}

.chart-tags-reset-btn:hover svg {
  transform: rotate(-180deg);
}

/* Glass-style Tooltip for Reset Button */
.chart-tags-reset-tooltip {
  position: absolute;
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
  padding: 8px 12px;
  background: rgba(238, 113, 37, 0.075);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: var(--text-primary);
  font-size: 11px;
  font-weight: 600;
  line-height: 1.4;
  white-space: nowrap;
  border-radius: 10px;
  box-shadow:
    0 4px 24px rgba(0, 0, 0, 0.12),
    0 0 16px rgba(238, 113, 37, 0.1);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.15s ease;
  z-index: 100;
}

.chart-tags-reset-tooltip::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 6px solid transparent;
  border-top-color: rgba(255, 255, 255, 0.3);
}

.chart-tags-reset-btn:hover .chart-tags-reset-tooltip {
  opacity: 1;
  visibility: visible;
}

/* Hide tooltip when confirm popup is open */
.chart-tags-reset-wrapper:has(.chart-tags-reset-confirm) .chart-tags-reset-tooltip {
  opacity: 0;
  visibility: hidden;
}

/* Glass-style Confirmation Popup */
.chart-tags-reset-confirm {
  position: absolute;
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
  padding: 12px 16px;
  background: rgba(238, 113, 37, 0.075);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  box-shadow:
    0 4px 24px rgba(0, 0, 0, 0.12),
    0 0 16px rgba(238, 113, 37, 0.1);
  z-index: 150;
  animation: confirmFadeIn 0.5s var(--ease-out-expo);
}

@keyframes confirmFadeIn {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(8px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0) scale(1);
  }
}

.chart-tags-reset-confirm::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 6px solid transparent;
  border-top-color: rgba(255, 255, 255, 0.3);
}

.chart-tags-reset-confirm p {
  margin: 0 0 10px 0;
  font-size: 12px;
  font-weight: 600;
  color: var(--text-primary);
  white-space: nowrap;
  text-align: center;
}

.confirm-buttons {
  display: flex;
  gap: 8px;
  justify-content: center;
}

.confirm-buttons button {
  padding: 6px 16px;
  font-size: 11px;
  font-weight: 600;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: all 0.15s ease;
}

.confirm-yes {
  background: rgba(238, 113, 37, 0.85);
  color: var(--white);
}

.confirm-yes:hover {
  background: rgba(238, 113, 37, 1);
}

.confirm-no {
  background: rgba(0, 0, 0, 0.06);
  color: var(--text-secondary);
}

.confirm-no:hover {
  background: rgba(0, 0, 0, 0.1);
  color: var(--text-primary);
}

.chart-container {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 32px;
  min-height: 420px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.04);
  transition: box-shadow var(--duration-slow) var(--ease-smooth);
}

.chart-container:hover {
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.07);
}

/* Responsive */
@media (max-width: 900px) {
  .dashboard-content {
    padding: 32px 24px 60px;
  }
  
  .metrics-row-cards {
    gap: 24px;
    flex-wrap: wrap;
  }
  
  .section-header h2 {
    font-size: 26px;
  }
}

@media (max-width: 600px) {
  .dashboard-content {
    padding: 24px 16px 48px;
  }
  
  .section-header h2 {
    font-size: 22px;
  }
  
  .section-header {
    margin-bottom: 32px;
  }
  
  .metrics-container {
    gap: 32px;
  }
  
  .metrics-row-cards {
    gap: 16px;
    padding: 12px;
  }
  
  .chart-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .chart-container {
    padding: 20px;
    border-radius: 20px;
  }
}